import {Component, ElementRef, EventEmitter, Input, NgZone, OnInit, Output} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {LibersignCertificate} from './certificate.model';
import {LibersignUtil} from './libersign.util';
import {LibersignNativeUtil} from './libersign.native.util';
import {LibersignAppletUtil} from './libersign.applet.util';

declare var LiberSign: any;

@Component({
  selector: 'ls-libersign',
  templateUrl: './libersign.component.html',
  styleUrls: ['./libersign.component.scss'],
})
export class LibersignComponent implements OnInit {

  constructor(private httpClient: HttpClient, private zone: NgZone, private element: ElementRef) { }

  @Input() updateUrl: string;
  @Input() appletUrl: string;
  @Output()
  sign: EventEmitter<LibersignCertificate> = new EventEmitter<LibersignCertificate>();
  @Output()
  cancel: EventEmitter<any> = new EventEmitter();
  @Output()
  signed: EventEmitter<any> = new EventEmitter();

  // @ts-ignore
  isIE = /*@cc_on!@*/false || !!document.documentMode;
  isValid = true;
  libersignUtil: LibersignUtil;

  ngOnInit() {
    if (typeof LiberSign !== 'undefined') {
      this.libersignUtil = new LibersignNativeUtil();
    } else if (this.isIE) {
      this.libersignUtil = new LibersignAppletUtil(this.zone);
    } else {
      this.libersignUtil = new LibersignAppletUtil(this.zone);
    }

    this.libersignUtil.init(this.updateUrl, this.appletUrl);

    this.libersignUtil.signatures.subscribe(sig => {
      this.signed.emit(sig);
    });
  }

  public onSign() {
    this.libersignUtil.status.dataToSignLoading = true;
    this.sign.emit(this.libersignUtil.currentCert);
  }

  public onCancel() {
    this.libersignUtil.destroy();
    this.cancel.emit();
  }

  @Input()
  set dataToSign(value: string[]) {
    // tslint:disable-next-line:triple-equals
    if (value != undefined && value.length > 0) {
      if (typeof value === 'string') {
        try {
          value = JSON.parse(value);
          this.libersignUtil.status.signLoading = true;
          this.libersignUtil.sign(value);
        } catch (error) {
          // Do nothing
        }
      } else {
        this.libersignUtil.status.signLoading = true;
        this.libersignUtil.sign(value);
      }
    }
  }

  @Input()
  set valid(value: string) {
    this.isValid = value === 'true';
  }
}
