import { AfterViewInit, Component, ElementRef, forwardRef, Input, Renderer2, ViewChild } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'ls-password',
  templateUrl: './password.component.html',
  styleUrls: ['./password.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PasswordComponent),
      multi: true
    }
  ],
})
export class PasswordComponent implements ControlValueAccessor, AfterViewInit {

  @Input() showIcon = true;
  @Input() required: boolean;
  @Input() trim = false;
  @ViewChild('input') input;
  @Input() name;
  isShown = false;

  constructor(private element: ElementRef, private renderer2: Renderer2) {
  }

  @Input() _value;

  get value() {
    return this._value;
  }

  set value(value) {
    if (this.trim) {
      this._value = value.trim();
    } else {
      this._value = value;
    }
    this.onChange(this._value);
    this.onTouched();
  }

  ngAfterViewInit(): void {
    const id = this.element.nativeElement.getAttribute('id');
    this.renderer2.removeAttribute(this.element.nativeElement, 'id');
    this.renderer2.setAttribute(this.input.nativeElement, 'id', id);
  }

  togglePassword() {
    this.isShown = !this.isShown;
  }

  onChange: any = () => {
    // tslint:disable-next-line:semicolon
  };

  onTouched: any = () => {
    // tslint:disable-next-line:semicolon
  };


  onChangeSelection() {
    this.onChange(this._value);
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  writeValue(value) {
    if (value) {
      this.value = value;
    }
  }

  trimValue(event) {
    if (this.trim) {
      event.target.value = event.target.value.trim();
    }
  }

}
