import {Component, EventEmitter, Input, Output} from '@angular/core';
import {LoginPageModelClass} from '../login-model/LoginPageModel.class';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'ls-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent extends LoginPageModelClass {

  username: string;
  email: string;
  usernameormail: string;

  @Input() oneFieldForgot = false;
  @Input() usernameormailInputName = 'usernameormail';
  @Input() usernameInputName = 'username';
  @Input() emailInputName = 'email';

  @Output()
  backToLogin: EventEmitter<any> = new EventEmitter();

  constructor(public _DomSanitizationService: DomSanitizer) {
    super(_DomSanitizationService);
  }

  backToLoginPage() {
    this.backToLogin.emit();
  }

  preventFormSubmit(event) {
    super.preventFormSubmit(event);
  }

  submitForm() {
    this.submit.emit({
      username: this.username,
      email: this.email,
      usernameormail: this.usernameormail
    });
  }
}
