import {Component, EventEmitter, Input, Output} from '@angular/core';
import {LoginPageModelClass} from '../login-model/LoginPageModel.class';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'ls-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent extends LoginPageModelClass {

  @Input() testMode = false;
  @Input() currentPage = 'login';

  @Input() loginFormAction = '';
  @Input() loginShowError = false;
  @Input() loginSuccessMessage = '';
  @Input() loginIsFormAjax = false;
  @Input() loginPasswordReset = false;
  @Input() loginShowRememberMe = false;
  @Input() loginUsernameInputName = 'username';
  @Input() loginPasswordInputName = 'password';
  @Input() loginRememberMeInputName = 'rememberme';

  @Input() forgotFormAction = '';
  @Input() forgotIsFormAjax = false;
  @Input() forgotOneFieldForgot = false;
  @Input() forgotUsernameInputName = 'username';
  @Input() forgotEmailInputName = 'email';
  @Input() forgotUsernameormailInputName = 'usernameormail';

  @Input() resetFormAction = '';
  @Input() resetIsFormAjax = false;
  @Input() resetPasswordInputName = 'password';
  @Input() resetPasswordConfirmInputName  = 'passwordconfirm';

  @Output()
  loginSubmit: EventEmitter<any> = new EventEmitter();
  @Output()
  forgotSubmit: EventEmitter<any> = new EventEmitter();
  @Output()
  resetSubmit: EventEmitter<any> = new EventEmitter();

  constructor(public _DomSanitizationService: DomSanitizer) {
    super(_DomSanitizationService);
  }

  login(event) {
    this.loginSubmit.emit(event);
  }

  forgot(event) {
    this.forgotSubmit.emit(event);
  }

  reset(event) {
    this.resetSubmit.emit(event);
  }
}
