import {Component, EventEmitter, Input, Output} from '@angular/core';
import {LoginPageModelClass} from '../login-model/LoginPageModel.class';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'ls-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})
export class LoginFormComponent extends LoginPageModelClass {

  username: string;
  password: string;
  rememberMe: boolean;

  @Input() showRememberMe = false;
  @Input() usernameInputName = 'username';
  @Input() passwordInputName = 'password';
  @Input() rememberMeInputName = 'rememberme';
  @Input() passwordReset = false;
  @Input() successMessage = '';
  @Output()
  forgotPassword: EventEmitter<any> = new EventEmitter();

  constructor(public _DomSanitizationService: DomSanitizer) {
    super(_DomSanitizationService);
  }

  askForResetPassword() {
    this.forgotPassword.emit();
  }

  preventFormSubmit(event) {
    super.preventFormSubmit(event);
  }

  submitForm() {
    this.submit.emit({
      username: this.username,
      password: this.password,
      rememberMe: this.rememberMe
    });
  }
}
