import {AfterViewInit, Component, ElementRef, forwardRef, Input, Renderer2, ViewChild} from '@angular/core';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';
import {MaxLengthTextAreaMessages} from './MaxLengthTextAreaMessages';

@Component({
  selector: 'ls-max-length-textarea',
  templateUrl: './max-length-textarea.component.html',
  styleUrls: ['./max-length-textarea.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => MaxLengthTextAreaComponent),
      multi: true
    }
  ]
})
export class MaxLengthTextAreaComponent implements ControlValueAccessor, AfterViewInit {

  @Input() maxlength: number;
  @Input() minlength: number;
  @Input() required: boolean;
  maxLengthTextAreaMessages = MaxLengthTextAreaMessages;

  @Input() _value;
  @Input() name;

  @ViewChild('input') input;

  constructor(private element: ElementRef, private renderer2: Renderer2) {}

  ngAfterViewInit(): void {
    const id = this.element.nativeElement.getAttribute('id');
    this.renderer2.removeAttribute(this.element.nativeElement, 'id');
    this.renderer2.setAttribute(this.input.nativeElement, 'id', id);
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
  }

  onChange: any = () => {
    // tslint:disable-next-line:semicolon
  };

  onTouched: any = () => {
    // tslint:disable-next-line:semicolon
  };

  onChangeSelection() {
    this.onChange(this._value);
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  writeValue(value) {
    if (value) {
      this.value = value;
    }
  }
}
