import {LibersignUtil} from './libersign.util';
import {LibersignCertificate} from './certificate.model';

declare var LiberSign: any;

export class LibersignNativeUtil extends LibersignUtil {
  init(updateUrl: string, appletUrl: string) {
    this.status.certsLoading = true;
    try {
      LiberSign.setUpdateUrl(updateUrl);
    } catch (error) {
      console.log('error catched');
    }
    this.loadCertificates();
  }

  loadCertificates() {
    LiberSign.getCertificates().then((cs: [LibersignCertificate]) => {
      if (cs !== undefined) {
        this.certificates = cs;
        this.selectCertificate(this.favorite);
        this.commonLoadCert();
      }
    }, err => {
      this.status.certsError = true;
    });
  }

  doSign(dataToSign: string[]) {
    LiberSign.sign(this.currentCert.ID, { dataToSign: dataToSign }).then(sig => {
      this.signatures.next(sig);
    });
  }

  selectCertificate(cert: LibersignCertificate) {
    this.currentCert = cert;
  }

  destroy() {
    // Nothing to do here
  }
}
