import {Component, ElementRef, OnInit} from '@angular/core';
import {SidebarService} from '../sidebar/sidebar.service';

@Component({
  selector: 'ls-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit {

  constructor(private el: ElementRef, private sidebarService: SidebarService) {
    this.sidebarService.collapse$
      .subscribe(isCollapsed => this.collapseEvent(isCollapsed));
  }

  ngOnInit() {}

  collapseEvent(isCollapsed) {
    if (isCollapsed) {
      this.el.nativeElement.classList.add('sidebar-collapsed');
    } else {
      this.el.nativeElement.classList.remove('sidebar-collapsed');
    }
  }

}
