export class LibersignStatus {
  installed = true;
  appletLoading = false;
  dataToSignLoading = false;
  signLoading = false;
  certsLoading = false;
  certsLoaded = false;
  certsError = false;

  isLoading(): boolean {
    return this.appletLoading || this.dataToSignLoading || this.certsLoading || this.signLoading;
  }

  showCerts(): boolean {
    return this.certsLoaded && !this.dataToSignLoading && !this.signLoading;
  }
}
