import {Component, EventEmitter, HostListener, Input, Output, ViewChild} from '@angular/core';
import {defaultConfiguration, LoginConfigModel} from './login-config.model';
import {DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'ls-login-config',
  templateUrl: './login-config.component.html',
  styleUrls: ['./login-config.component.scss']
})
export class LoginConfigComponent {

  @Output()
  save: EventEmitter<LoginConfigModel> = new EventEmitter<LoginConfigModel>();
  @Output()
  back: EventEmitter<any> = new EventEmitter();

  @ViewChild('fileInput') fileInput;

  showPrevisu = false;
  saving = false;
  isSaved = false;

  constructor(public _DomSanitizationService: DomSanitizer) {
    this._configuration = Object.assign({}, defaultConfiguration);
  }

  _configuration: LoginConfigModel;
  _asyncSave = false;
  _timeoutShow;

  @HostListener('saved') onSaved() {
    this.isSaved = true;
    this.saving = false;
    clearTimeout(this._timeoutShow);
    this._timeoutShow = setTimeout(() => {
      this.isSaved = false;
    }, 3000);
  }

  @Input()
  set asyncSave(async) {
    this._asyncSave = typeof async === 'string' ? async === 'true' : async;
  }

  @Input()
  set configuration(config) {
    this._configuration = typeof config === 'string' ? JSON.parse(config) : config;
  }

  public stopPropagation(event: any): void {
    event.stopPropagation();
  }

  @HostListener('document:keydown.escape')
  public onEscapeKeyPressed() {
    this.showPrevisu = false;
  }

  public onFileChanged(event) {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.addEventListener('load', () => {
      this._configuration.imgName = file.name;
      // @ts-ignore
      this._configuration.imgData = reader.result;
    }, false);

    if (file) {
      reader.readAsDataURL(file);
    }
  }

  removeCurrentFile() {
    this._configuration.imgData = undefined;
    this.fileInput.nativeElement.value = '';
  }

  public onReset() {
    this.removeCurrentFile();
    Object.assign(this._configuration, defaultConfiguration);
  }

  public onSave() {
    this.save.emit(this._configuration);
    if (this._asyncSave) {
      this.saving = true;
    } else {
      this.onSaved();
    }
  }

  public onBack() {
    this.back.emit();
  }

}
