import {
  AfterViewInit,
  Component,
  ElementRef,
  forwardRef,
  HostBinding,
  Input,
  OnInit,
  Renderer2,
  ViewChild
} from '@angular/core';
import {animate, animateChild, group, query, state, style, transition, trigger} from '@angular/animations';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';

@Component({
  selector: 'ls-timepicker',
  templateUrl: './timepicker.component.html',
  styleUrls: ['./timepicker.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => TimepickerComponent),
    multi: true
  }],
  animations: [
    trigger('fadeInOut', [
      transition('void => *', [
        style({
          opacity: 0
        }),
        group([
          animate(300, style({
            opacity: 1
          })),
          query('@popOut', animateChild())
        ])]),
      transition('* => void', [
        style({
          opacity: 1
        }),
        group([
          animate(300, style({
            opacity: 0
          })),
          query('@popOut', animateChild())
        ])]),
    ]),
    trigger('popOut', [
      // ...
      state('void', style({
        transform: 'scale(0.6)'
      })),
      transition('void <=> *', animate(300)),
    ])
  ],
})
export class TimepickerComponent implements OnInit, AfterViewInit, ControlValueAccessor {

  hoursElements = [
    'subrot270',
    'rot300',
    'rot330',
    'rot0',
    'rot30',
    'rot60',
    'rot90',
    'rot120',
    'rot150',
    'rot180',
    'rot210',
    'rot240',
    'rot270',

    'subrot300',
    'subrot330',
    'subrot0',
    'subrot30',
    'subrot60',
    'subrot90',
    'subrot120',
    'subrot150',
    'subrot180',
    'subrot210',
    'subrot240',
  ];

  minutesElements = [
    'rot270',
    'rot300',
    'rot330',
    'rot0',
    'rot30',
    'rot60',
    'rot90',
    'rot120',
    'rot150',
    'rot180',
    'rot210',
    'rot240',
  ];

  hour = 0;
  minute = 0;

  rot = 0;

  showWrapper = false;
  updateMode = false;
  hourMode = true;

  @Input() disabled = false;

  @ViewChild('input') input;
  @HostBinding('class.input-group') classInputGroup = true;
  @HostBinding('class.input-group-end-icon') classInputGroupEndIcon = true;

  constructor(private el: ElementRef, private renderer2: Renderer2) {
  }

  ngAfterViewInit(): void {
    this.moveAttributeToInput('id');
    this.moveAttributeToInput('tabindex');
    this.moveAttributeToInput('required', '');
    this.moveAttributeToInput('name', 'timepicker');
  }

  private moveAttributeToInput(attributeName: string, defaultValue?: string) {
    const attribute = this.el.nativeElement.getAttribute(attributeName);
    if (attribute !== null) {
      this.renderer2.removeAttribute(this.el.nativeElement, attributeName);
    }
    if (attribute !== null || defaultValue) {
      this.renderer2.setAttribute(this.input.nativeElement, attributeName, attribute ? attribute : defaultValue);
    }
  }

  ngOnInit() {
    this.validate();
  }

  pad(num): string {
    return '00'.substring(0, 2 - ('' + num).length) + num;
  }

  show() {
    this.showWrapper = true;
    setTimeout(() => this.setHour(this.hour), 0);
  }

  rotateThis(nR) {
    let aR;
    this.rot = this.rot || 0; // if rot undefined or 0, make 0, else rot
    aR = this.rot % 360;
    if (aR < 0) {
      aR += 360;
    }
    if (aR < 180 && (nR > (aR + 180))) {
      this.rot -= 360;
    }
    if (aR >= 180 && (nR <= (aR - 180))) {
      this.rot += 360;
    }
    this.rot += (nR - aR);
    return 'rotate( ' + this.rot + 'deg )';
  }

  setHour(hour) {
    let transform = this.rotateThis((hour * 30) % 360);
    if (hour === 0 || hour > 12) {
      transform += ' scale(0.6)';
    }
    this.el.nativeElement.getElementsByClassName('time-picker-clock-arrow')[0].style.transform = transform;
    this.hour = hour % 24;
  }

  setMinute(minute) {
    this.minute = minute % 60;
    this.el.nativeElement.getElementsByClassName('time-picker-clock-arrow')[0].style.transform = this.rotateThis(minute / 5 * 30);
  }

  mousedown() {
    this.updateMode = true;
  }

  toHourMode() {
    this.hourMode = true;
    this.setHour(this.hour);
  }

  toMinuteMode() {
    this.hourMode = false;
    this.setMinute(this.minute);
  }

  mouseup(event) {
    this.move(event);
    this.updateMode = false;
    if (this.hourMode) {
      this.toMinuteMode();
    }
  }

  move(event) {
    if (this.updateMode && event.target instanceof HTMLButtonElement) {
      const btn: HTMLButtonElement = event.target;

      const nodes = Array.prototype.slice.call(btn.parentElement.children);
      const index = nodes.indexOf(btn);
      if (this.hourMode) {
        this.setHour(index);
      } else {
        this.setMinute(index * 5);
      }
    }
  }

  hide() {
    this.showWrapper = false;
    this.updateMode = false;
    this.hourMode = true;
  }

  validate() {
    this.writeValue(this.pad(this.hour) + ':' + this.pad(this.minute));
    this.el.nativeElement.getElementsByTagName('input')[0].value = this.pad(this.hour) + ':' + this.pad(this.minute);
    this.hide();
  }

  inputChange($event) {
    this.onChange($event.currentTarget.value);
  }

  // noinspection JSUnusedLocalSymbols
  onChange = (time: string) => {
    // tslint:disable-next-line:semicolon
  };

  onTouched = () => {
    // tslint:disable-next-line:semicolon
  };

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  writeValue(obj: any): void {
    if (obj) {
      this.el.nativeElement.getElementsByTagName('input')[0].value = obj;
      if (typeof obj === 'string') {
        const time = obj.split(':');
        this.hour = +time[0];
        this.minute = +time[1];
      }
    }
    this.onChange(obj);
  }
}
