import {EventEmitter, Input, Output} from '@angular/core';
import {DomSanitizer} from '@angular/platform-browser';
import {defaultConfiguration, LoginConfigModel} from '../login-config/login-config.model';

export class LoginPageModelClass {

  submitted = false;

  @Input() formAction = '';
  @Input() logo;
  @Input() isFormAjax = false;
  @Input() showError = false;

  @Output()
  submit: EventEmitter<any> = new EventEmitter();

  constructor(public _DomSanitizationService: DomSanitizer) {
  }

  _visualConfiguration: LoginConfigModel = defaultConfiguration;

  @Input()
  set visualConfiguration(config) {
    this._visualConfiguration = typeof config === 'string' ? JSON.parse(config) : config;
  }

  preventFormSubmit(event) {
    if (this.isFormAjax) {
      this.submitted = true;
      event.preventDefault();
    }
  }
}
