import { LibersignStatus } from './libersign-status.model';
import {LibersignCertificate} from './certificate.model';
import {BehaviorSubject} from 'rxjs';

export abstract class LibersignUtil {
  status: LibersignStatus = new LibersignStatus();

  certificates: [LibersignCertificate];
  currentCert: LibersignCertificate;
  _favorite: LibersignCertificate;

  FAVORITEKEY = 'libersign-fav';
  signatures;
  _signatures = [];

  abstract init(updateUrl: string, appletUrl: string);
  abstract loadCertificates();
  abstract selectCertificate(cert: LibersignCertificate);
  abstract doSign(dataToSign: string[]);
  abstract destroy();

  constructor() {
    this.signatures = new BehaviorSubject<any[]>(this._signatures);
  }

  set favorite(cert: LibersignCertificate) {
    localStorage.setItem(this.FAVORITEKEY, cert.ID);
    this._favorite = cert;
  }
  get favorite() {
    if (this._favorite !== undefined) {
      return this._favorite;
    } else {
      const id = localStorage.getItem(this.FAVORITEKEY);
      for (const cert of this.certificates) {
        if (cert.ID === id) {
          this._favorite = cert;
          return cert;
        }
      }
    }
    return undefined;
  }

  sign(dataToSign: string[]) {
    this.status.dataToSignLoading = false;
    this.status.signLoading = true;
    this.doSign(dataToSign);
  }

  commonLoadCert() {
    this.status.certsLoading = false;
    this.status.certsLoaded = true;
  }

}
